.country-name-box {
    .country-name {
        color: rgb(184, 184, 184);
        cursor: pointer;
        font-size: 2.2rem;
        width: fit-content;
        user-select: none;

        &:hover {
            color: white;
        }
    }

    .country-active {
        color: white;
    }
}
