.enquiry-container {
    background: black;
    width: 100vw;
    height: 100vh;
    overflow: hidden;

    .enquiry-main-container {
        width: 100%;
        height: 100%;
        padding: 40px 40px;

        display: flex;
        justify-items: center;
        align-items: center;

        .enquiry-main {
            background: url("../../images/enquiryForm.png");
            background-position: center;
            background-size: cover;
            width: 100%;
            height: 100%;

            padding: 40px;

            display: flex;

            .enquiry-main-left,
            .enquiry-main-right {
                width: 60%;
                height: 100%;
            }

            .enquiry-main-left {
                display: flex;
                flex-direction: column;
                justify-content: space-between;

                .logo {
                    font-family: "Shoreline";
                    font-size: 1.4rem;
                    margin-right: 60px;
                }

                .form-title {
                    font-size: 5rem;
                    font-weight: 700;
                    margin-right: 60px;
                }

                .contacts {
                    margin-right: 60px;
                    .contact-title {
                        font-size: 1.6rem;
                        font-weight: 700;
                    }
                    .contact {
                        font-size: 1.4rem;
                        a {
                            text-decoration: none;
                            color: white;

                            &:hover {
                                color: goldenrod;
                            }
                        }
                    }
                }
            }

            .enquiry-main-right {
                background-color: black;
                width: 100%;
                padding: 30px;

                display: flex;
                flex-direction: column;
                gap: 40px;

                .enquiry-main-right-title {
                    text-align: center;
                    color: #686868;
                    font-size: 1.8rem;
                    font-weight: 700;
                }

                .enquiry-form-message-area {
                    min-width: 100%;
                    max-width: 100%;
                    resize: none;
                    background-color: transparent;
                    border: 1px solid #686868;
                    padding: 10px;
                    height: 100%;
                    color: white;
                    font-size: 1.4rem;

                    &:focus {
                        outline: none;
                    }
                }

                .enquiry-form-inputs-container {
                    display: flex;
                    flex-direction: column;
                    gap: 30px;

                    .enquiry-form-inputs {
                        .enquiry-form-input-title {
                            color: #686868;
                            font-size: 2rem;
                            font-weight: 500;
                        }

                        .enquiry-form-inputs-main {
                            margin-top: 14px;
                            display: grid;
                            grid-template-columns: 1fr 1fr;
                            column-gap: 40px;
                            row-gap: 20px;

                            input {
                                font-size: 1.4rem;
                                background-color: transparent;
                                border: none;
                                border-bottom: 1px solid #686868;
                                padding-bottom: 6px;
                                color: white;

                                &:focus {
                                    outline: none;
                                }
                            }
                        }
                    }
                }

                .enquiry-form-buttons {
                    display: flex;
                    justify-content: flex-end;
                    gap: 20px;

                    button {
                        background-color: transparent;
                        color: white;
                        border: 1px solid #686868;
                        padding: 10px 20px;
                        cursor: pointer;
                        transition: all 0.16s linear;

                        &:hover {
                            background-color: rgb(67, 48, 0);
                        }
                    }

                    .submit {
                        background: rgb(50, 40, 12);
                    }
                }
            }
        }
    }
}

@media (max-width: 1148px) {
    .enquiry-container {
        height: fit-content;
        .enquiry-main-container {
            .enquiry-main {
                flex-direction: column;

                .enquiry-main-left {
                    width: 100%;

                    .logo {
                        display: none;
                    }

                    .form-title {
                        margin-right: 0px;
                        text-align: center;
                        font-size: 4rem;
                    }

                    .contacts {
                        visibility: hidden;
                    }
                }

                .enquiry-main-right {
                    .enquiry-form-message-area {
                        min-height: 120px;
                    }
                }
            }
        }
    }
}

@media (max-width: 608px) {
    .enquiry-container {
        .enquiry-main-container {
            .enquiry-main {
                .enquiry-main-right {
                    .enquiry-form-buttons {
                        button {
                            font-size: 1.2rem;
                        }
                    }
                }
            }
        }
    }
}

@media (max-width: 482px) {
    .enquiry-container {
        .enquiry-main-container {
            padding: 0px;
            .enquiry-main {
                .enquiry-main-right {
                    .enquiry-form-inputs-container {
                        .enquiry-form-inputs {
                            .enquiry-form-inputs-main {
                                grid-template-columns: 1fr;
                                row-gap: 10px;
                            }
                        }
                    }
                }
            }
        }
    }
}
